import { Grid, Typography } from '@material-ui/core';

import logo from './weatherlog-mini-logo.svg';
import { NavigateBackButton } from 'src/components';
import useStyles from './styles';

export const AboutPage = () => {
	const classes = useStyles();

	return (
    <Grid container item direction="column" className={classes.root}>
      <Grid item container className={classes.titleContainer}>
        <NavigateBackButton page="back" />
        <Typography variant="h2" className={classes.title}>
          Om
        </Typography>
      </Grid>
      <Grid item className={classes.textBodyContainer}>
        <Typography variant="body1">
          Vderdagboken r en digital dagbok fr att logga vdret hos dig. Du
          kan med Vderdagboken logga dagar, ndra och ta bort dem. Se
          historiken per mnad i diagram-form och listvy men ven alla loggar i
          en enda list-vy. Det r inte bara personer som kan anvnda den hr
          webb-appen utan ven skolklasser som vill se hur vdret frndras inom
          en viss period. Alla som r intresserade av vdret!
        </Typography>
        <img src={logo} alt="Logo" className={classes.logo} />
      </Grid>
      <Grid item>
        <Typography variant="h5" className={classes.subtitle}>
          Id
        </Typography>
        <Typography variant="body1">
          Skaparna r Nicklas Holmqvist och Oliver Nygren som ett examensarbete
          i Frontendutbildningen 2022 p Medieinstitutet i Gteborg. Grundidn
          till Vderdagboken var ngot som Nicklas hade med sig som barn d han
          satt och skapade egna diagram i ett rut-block. Nu kan man logga det
          mesta som en vderstation kan visa.
        </Typography>
      </Grid>
      <Grid item>
        <Typography variant="h5" className={classes.subtitle}>
          Rttigheter
        </Typography>
        <Typography variant="subtitle1">
          Copyright  2022 | Vderdagboken
        </Typography>
        <Typography variant="body1">Alla rttigheter reserverade</Typography>
      </Grid>
    </Grid>
  );
};

export default AboutPage;
